<template>
  <div>
    {{msg}}
    <div id="map" class="map"></div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'domestic-map',
  computed: {},
  components: {
  },
  data() {
    return {
      msg: 'map',
    }
  },
  mounted() {
    this.load()
  },
  methods: {
    load() {
      var mapContainer = document.getElementById('map'), //   div 
        mapOption = {
          center: new daum.maps.LatLng(33.450701, 126.570667), //  
          level: 3 //   
        };

      //   div      
      var map = new daum.maps.Map(mapContainer, mapOption);
    }
  }
}
</script>

<style>
.map {
  width: 500%;
  /* position: relative; */
  height: 100px;
}
</style>


